<template>
  <FormsContainer class="container">
    <VTypography class="mb-5" font-weight="heavy" font-style="header-1"
      >Общая информация</VTypography
    >

    <div class="row mb-2">
      <div class="col">
        <TextInput
          label="Направлено"
          :disabled="!editable"
          placeholder="Русский текст"
          v-model="russian"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium"
                >Фамилия Имя Отчество полностью</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          v-model="english"
          root-class="mt-4"
          :disabled="!editable"
          placeholder="Английский текст"
        />
      </div>
    </div>

    <div class="row">
      <div class="col">
        <TextInput
          type="email"
          v-model="email"
          :multiline="false"
          placeholder="Email"
          :disabled="!editable"
        />
      </div>
      <div class="col">
        <TextInput
          v-model="technology"
          :disabled="!editable"
          placeholder="Технология"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import { isEditableStatus } from "@/helpers/StatusHelpers";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";

export default {
  name: "GeneralForm",
  components: {
    TextInput,
    VTypography,
    PopoverContent,
    FormsContainer,
  },
  methods: {
    ...mapMutations("general", [
      "changeEmail",
      "changeTechnology",
      "changeRussianInformation",
      "changeEnglishInformation",
    ]),
  },
  computed: {
    ...mapGetters("formInfo", { formStatus: "getStatus" }),
    ...mapGetters("general", [
      "getEmail",
      "getTechnology",
      "getEnglishInformation",
      "getRussianInformation",
    ]),
    editable() {
      return isEditableStatus(this.formStatus);
    },
    russian: {
      get() {
        return this.getRussianInformation;
      },
      set(value) {
        this.changeRussianInformation(value);
      },
    },
    english: {
      get() {
        return this.getEnglishInformation;
      },
      set(value) {
        this.changeEnglishInformation(value);
      },
    },
    email: {
      get() {
        return this.getEmail;
      },
      set(value) {
        this.changeEmail(value);
      },
    },
    technology: {
      get() {
        return this.getTechnology;
      },
      set(value) {
        this.changeTechnology(value);
      },
    },
  },
};
</script>
