<template>
  <div class="d-flex flex-column">
    <TypographyWithInfo class="mb-5" font-weight="heavy">
      Сведения о творческом вкладе Авторов

      <template v-slot:popover>
        <PopoverContent :width="238">
          <VTypography font-weight="medium"
            >В столбце сумма процентов по всем строкам должна составлять 100
            (сто) %</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <table
      v-if="authors.length > 0"
      class="authors-creative-table__table v-table"
    >
      <thead>
        <tr class="v-table__row v-table__header-row">
          <th
            class="v-table__cell text-center authors-creative-table__header-number"
          >
            №
          </th>
          <th class="v-table__cell border-end-0">ФИО</th>
          <th
            class="v-table__cell text-center authors-creative-table__contribution-cell"
            :class="{
              'authors-creative-table__contribution-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__contribution-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            %
          </th>
          <th class="v-table__cell">Должность</th>
          <th class="v-table__cell">Наименование Центра (место работы)</th>
        </tr>
      </thead>

      <tbody>
        <tr
          :key="item.name"
          v-for="(item, index) in authors"
          class="v-table__row v-table__body-row"
        >
          <td class="v-table__cell text-center">
            {{ index + 1 }}
          </td>
          <td class="v-table__cell border-end-0">
            {{ item.name.russian }}
          </td>
          <td
            class="v-table__cell authors-creative-table__contribution-cell"
            :class="{
              'authors-creative-table__contribution-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__contribution-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            <BaseInput
              :maxlength="3"
              class="text-center p-0"
              :value="item.creative || 0"
              @input="
                updateCreative({ id: item.id, value: $event.target.value })
              "
            />
          </td>
          <td class="v-table__cell">
            {{ item.position?.russian }}
          </td>
          <td class="v-table__cell">
            {{ item.workplace?.russian }}
          </td>
        </tr>
        <tr class="authors-creative-table__result-row">
          <td colspan="2" class="text-start p-1">
            <TypographyWithInfo position="left" class="d-block">
              ИТОГО

              <template v-slot:popover>
                <PopoverContent :width="238">
                  <VTypography font-weight="heavy"
                    >В столбце сумма процентов по всем строкам должна составлять
                    100 (сто) %</VTypography
                  >
                </PopoverContent>
              </template>
            </TypographyWithInfo>
          </td>
          <td
            class="authors-creative-table__contribution-cell text-center p-1"
            :class="{
              'authors-creative-table__contribution-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__contribution-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            {{ creativeInfo.sum }}
          </td>
          <td colspan="2" class="p-1">
            <VTypography
              v-if="creativeInfo.show"
              font-weight="medium"
              class="authors-creative-table__error"
              >Должно быть 100%</VTypography
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import BaseInput from "@/components/ui/BaseInput";
import VTypography from "@/components/ui/Typography";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "AuthorsCreativeTable",
  components: { VTypography, BaseInput, PopoverContent, TypographyWithInfo },
  computed: {
    ...mapGetters("authors", {
      authors: "getAllAuthors",
    }),
    creativeInfo() {
      let sum = 0;
      let allMoreZero = true;

      this.authors.forEach((x) => {
        if (!x.creative) {
          allMoreZero = false;
        }

        sum = sum + x.creative || 0;
      });

      return {
        sum,
        show: sum !== 100 && allMoreZero,
      };
    },
  },
  methods: {
    ...mapMutations("authors", ["updateCreative"]),
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.authors-creative-table__header-number {
  width: 60px;
}

.authors-creative-table__contribution-cell {
  @include transition(border-color);

  width: 60px;
  max-width: 60px;
  border: 2px solid $red;
}

.authors-creative-table__contribution-cell--success {
  border-color: $green;
}

.authors-creative-table__contribution-cell--fail {
  border-color: $red;
}

.authors-creative-table__result-row {
  border-bottom: 2px solid $gray5;
}

.authors-creative-table__error {
  color: $red;
}
</style>
