<template>
  <VButton
    @mouseenter="hover = true"
    @mouseleave="hover = false"
    class="d-flex align-items-center justify-content-center close-button"
    :class="{
      'close-button--secondary': type === 'secondary',
    }"
  >
    <CrossIcon :color="iconColor" />
  </VButton>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import CrossIcon from "@/components/icons/CrossIcon";

export default {
  name: "CloseButton",
  components: { CrossIcon, VButton },
  props: {
    type: {
      type: String,
      default: "primary",
      validate(value) {
        return ["primary", "secondary"].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
    };
  },
  computed: {
    iconColor() {
      switch (true) {
        case this.type === "secondary" && this.hover:
          return "#161615";

        case this.type === "secondary" && !this.hover:
          return "#999999";

        default:
          return "#ffffff";
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.close-button {
  width: 32px;
  height: 32px;
  border: 2px solid $gray2;

  &:hover {
    border-color: $white;
  }
}

.close-button--secondary {
  border-color: $gray4;

  &:hover {
    border-color: $gray1;
  }
}
</style>
