<template>
  <VModal class="justify-content-end" position="right" :show="show">
    <form @submit.prevent="onSubmit">
      <ModalContainer
        title="Добавить иного автора"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <TextInput
            label="ФИО"
            input-type="contrast"
            :maxlength="100"
            :error="errors.russianName"
            placeholder="Русский текст"
            v-model="author.russianName"
            name="author.russianName"
            root-class="mb-2"
            popoverPosition="top-right"
            info-text="Какой-то будет текст подсказки"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="other-author-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то будет текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.englishName"
            v-model="author.englishName"
            name="author.englishName"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            label="Место работы"
            popover-color="#272726"
            :info-popover-width="266"
            :error="errors.workplace.russian"
            placeholder="Русский текст"
            v-model="author.workplace.russian"
            root-class="mb-2"
            info-text="Какой-то будет текст подсказки"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.workplace.english"
            v-model="author.workplace.english"
            name="author.workplace.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            label="Подразделение"
            popover-color="#272726"
            :info-popover-width="266"
            :error="errors.division.russian"
            placeholder="Русский текст"
            v-model="author.division.russian"
            name="author.division.russian"
            root-class="mb-2"
            info-text="Какой-то будет текст подсказки"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.division.english"
            v-model="author.division.english"
            name="author.division.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            label="Должность"
            popover-color="#272726"
            :info-popover-width="266"
            :error="errors.position.russian"
            placeholder="Русский текст"
            v-model="author.position.russian"
            name="author.position.russian"
            root-class="mb-2"
            info-text="Какой-то будет текст подсказки"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.position.english"
            v-model="author.position.english"
            name="author.position.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TypographyWithInfo
            :info-popover-width="266"
            info-text="Какой-то будет текст подсказки"
            class="text-white mb-1"
            >Гражданство</TypographyWithInfo
          >

          <RadioGroup
            v-model="author.isCitizen"
            name="author.isCitizen"
            type="contrast"
            :items="[
              { key: true, title: 'РФ' },
              { key: false, title: 'Другое' },
            ]"
            :class="{
              'mb-5': author.isCitizen,
              'mb-1': !author.isCitizen,
            }"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            placeholder="Русский текст"
            v-if="!author.isCitizen"
            root-class="mb-2"
            :error="errors.otherCitizenship.russian"
            v-model="author.otherCitizenship.russian"
            name="author.otherCitizenship.russian"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            v-if="!author.isCitizen"
            :error="errors.otherCitizenship.english"
            v-model="author.otherCitizenship.english"
            name="author.otherCitizenship.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TypographyWithInfo class="mb-1 other-author-modal__birthday-title">
            Дата рождения
            <template v-slot:popover>
              <PopoverContent :width="266" class="other-author-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то будет текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TypographyWithInfo>

          <DatePicker v-model="author.birthday" class="mb-5" />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            label="Зарегистрирован по адресу"
            popover-color="#272726"
            :info-popover-width="266"
            :error="errors.registerAddress.russian"
            placeholder="Русский текст"
            v-model="author.registerAddress.russian"
            name="author.registerAddress.russian"
            root-class="mb-2"
            info-text="Какой-то будет текст подсказки"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.registerAddress.english"
            v-model="author.registerAddress.english"
            name="author.registerAddress.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            label="Почтовый адрес"
            popover-color="#272726"
            :info-popover-width="266"
            :error="errors.postAddress.russian"
            placeholder="Русский текст"
            v-model="author.postAddress.russian"
            name="author.postAddress.russian"
            root-class="mb-2"
            info-text="Какой-то будет текст подсказки"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.postAddress.english"
            v-model="author.postAddress.english"
            name="author.errors.postAddress.english"
            placeholder="Английский текст"
            root-class="mb-5"
          />

          <TypographyWithInfo class="mb-1">
            ИНН
            <template v-slot:popover>
              <PopoverContent :width="266" class="other-author-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то будет текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TypographyWithInfo>

          <InnInput class="mb-5" />
        </template>

        <template v-slot:footer>
          <ActionButton button-type="outline" @click="onSubmit">{{
            buttonTitle
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import {
  getOtherAuthorsInitialErrors,
  getOtherAuthorsInitialValues,
} from "@/helpers/AuthorsHelpers";
import InnInput from "@/components/ui/InnInput";

export default {
  name: "OtherAuthorModal",
  components: {
    InnInput,
    DatePicker,
    PopoverContent,
    RadioGroup,
    TypographyWithInfo,
    TextInput,
    VTypography,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => getOtherAuthorsInitialValues(),
    },
  },
  data() {
    return {
      errors: getOtherAuthorsInitialErrors(),
      author: getOtherAuthorsInitialValues(),
    };
  },
  computed: {
    buttonTitle() {
      return this.initialValues.russianName || this.initialValues.englishName
        ? "Изменить автора"
        : "Добавить автора";
    },
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      this.errors = {};

      const hasRussianName = !!this.author.russianName;
      const hasEnglishName = !!this.author.englishName;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }

      if (hasRussianName && hasEnglishName) {
        this.$emit("submit", this.author);
        this.author = getOtherAuthorsInitialValues();
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.other-author-modal__popover {
  background-color: $gray3;
}

.other-author-modal__birthday-title {
  color: $white;
}
</style>
