<template>
  <div class="d-flex position-relative flex-column v-switch">
    <div
      v-if="!!modelValue"
      class="position-absolute v-switch__indicator"
      :style="{ width, transform }"
    />

    <div class="d-flex flex-fill">
      <VButton
        ref="buttons"
        :key="item.key"
        class="d-flex user-select-none align-items-center justify-content-center px-1 v-switch__item"
        v-for="item in items"
        @click="click(item.key)"
        :class="{ 'v-switch__item--active': modelValue === item.key }"
      >
        <VTypography
          font-weight="heavy"
          class="text-uppercase v-switch__item-title"
          >{{ item.title }}</VTypography
        >
      </VButton>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";

export default {
  name: "VSwitch",
  components: { VButton, VTypography },
  props: {
    items: Array,
    modelValue: String,
  },
  data() {
    return {
      width: 0,
      transform: "translateX(0)",
    };
  },
  mounted() {
    this.animate(0);
  },
  watch: {
    modelValue(value) {
      const valueIndex = this.items.findIndex((x) => x.key === value);

      const index = valueIndex >= 0 ? valueIndex : 0;

      this.animate(index);
    },
  },
  methods: {
    animate(index) {
      const prevIndex = index > 0 ? index - 1 : -1;

      const currentRef = this.$refs.buttons[index].$refs.vButton;
      const prevRef =
        prevIndex >= 0
          ? this.$refs.buttons[prevIndex].$refs.vButton
          : undefined;

      if (prevRef) {
        this.transform = `translateX(${prevRef?.clientWidth}px)`;
      } else {
        this.transform = "translateX(0)";
      }

      this.width = `${currentRef?.clientWidth + 2}px`;
    },
    click(key) {
      this.$emit("update:modelValue", key);
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.v-switch {
  height: 32px;
  border: 2px solid $gray1;
}

.v-switch__item {
  cursor: pointer;

  &:not(:first-child) {
    border-left: 1px solid $gray1;
  }

  &:not(:last-child) {
    border-right: 1px solid $gray1;
  }
}

.v-switch__item--active {
  & > .v-switch__item-title {
    color: $white;
  }
}

.v-switch__item-title {
  @include transition(color);

  color: $gray1;
}

.v-switch__indicator {
  @include transition(transform);

  top: 0;
  bottom: 0;
  z-index: -1;
  background-color: $gray1;
}
</style>
