<template>
  <div class="container">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      Дата предполагаемого опубликования и/или устного/письменного раскрытия, а
      также иные сведения о предполагаемых опубликованиях/раскрытиях, если
      таковые планируются

      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">Подсказка</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleasePlaned"
      class="mb-5"
      :items="[
        { key: true, title: 'Да' },
        { key: false, title: 'Нет' },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleasePlaned"
      class="mb-1"
    >
      Дата

      <template v-slot:popover>
        <PopoverContent :width="440">
          <VTypography font-weight="medium"
            >Дата начала практических работ над созданием
            Технологии.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker
      v-if="hasReleasePlaned"
      v-model="releasePlanedDate"
      class="mb-5"
    />

    <div class="row" v-if="hasReleasePlaned">
      <div class="col">
        <TextInput
          v-model="releasePlanedRussianInformation"
          info-text="Подсказка"
          label="Иные сведения"
          :info-popover-width="350"
          placeholder="Русский текст"
        >
          <template v-slot:popover>
            <PopoverContent :width="350">
              <VTypography font-weight="medium">Подсказка</VTypography>
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          v-model="releasePlanedEnglishInformation"
          placeholder="Английский текст"
          root-class="mt-4"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "DisclosurePlanedRelease",
  components: {
    TextInput,
    DatePicker,
    RadioGroup,
    VTypography,
    PopoverContent,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("disclosure", [
      "getHasReleasePlaned",
      "getReleasePlanedDate",
      "getReleasePlanedRussianInformation",
      "getReleasePlanedEnglishInformation",
    ]),
    hasReleasePlaned: {
      get() {
        return this.getHasReleasePlaned;
      },
      set(value) {
        this.changeHasReleasePlaned(value);
      },
    },
    releasePlanedDate: {
      get() {
        return this.getReleasePlanedDate;
      },
      set(value) {
        this.changeReleasePlanedDate(value);
      },
    },
    releasePlanedRussianInformation: {
      get() {
        return this.getReleasePlanedRussianInformation;
      },
      set(value) {
        this.changeReleasePlanedRussianInformation(value);
      },
    },
    releasePlanedEnglishInformation: {
      get() {
        return this.getReleasePlanedEnglishInformation;
      },
      set(value) {
        this.changeReleasePlanedEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleasePlaned",
      "changeReleasePlanedDate",
      "changeReleasePlanedRussianInformation",
      "changeReleasePlanedEnglishInformation",
    ]),
  },
};
</script>
